import React from 'react';
import './style/App.css';
import TransactionList from './TransactionList';
import TransactionForm from './TransactionForm';
import Valute from './Valute';
import axios from "axios";

class App extends React.Component {
  constructor(){
    super();
    this.state ={
      valute: 1,
      defValute: 0,
      listTrans: [],
      actualValueInputValute: '',
      actualValueInputCost: ''
    }
}
  componentDidMount(){
    axios.get('http://api.nbp.pl/api/exchangerates/rates/A/EUR/').then(
          (res)=>{
            this.setState({
              defValute: parseFloat(res.data.rates[0].mid),
              valute: parseFloat(res.data.rates[0].mid)
            })
          }).catch(()=>{
            console.log("Problem z pobaniem danych");
          })
  }

  addTrans = (e) => {
      e.preventDefault() 
      const name = e.currentTarget.Form_Name.value;
      const costTrans = e.currentTarget.Form_Cost.value;
      const validationName = document.querySelector('.TransactionForm__NameValidationSpan');
      const validationCost = document.querySelector('.TransactionForm__CostValidationSpan');
      const formNameInput = document.querySelector('#Form_Name');
      const formCostInput = document.querySelector('#Form_Cost');

        if(name.length === 0){
          if(costTrans.length !== 0 && costTrans !== '.' ){
            validationCost.style.display='none';
            formCostInput.style= "border-color:var(--border-color)";
          }else{
            validationCost.style.display='block';
            formCostInput.style= "border-color:var(--validation-color);";
          }
          validationName.innerHTML = " PODAJ NAZWĘ TRANSAKCJI";
          validationName.style.display='block';
          formNameInput.style= "border-color:var(--validation-color);";
          
          if(costTrans.length === 0){
            if(name.length !== 0  && name.charAt(0,1) !== ' '){
              validationName.style.display='none';
              formNameInput.style= "border-color:var(--validation-color)";
            }
            validationCost.innerHTML = "PODAJ KWOTĘ TRANSAKCJI !"
            validationCost.style.display='block';
            formCostInput.style= "border-color:var(--validation-color);";
          }else if(costTrans === '.'){
            if(name.length !== 0  && name.charAt(0,1) !== ' '){
              validationName.style.display='none';
              formNameInput.style= "border-color:var(--border-color)";
            }
            validationCost.innerHTML = "PODAJ PRAWIDŁOWĄ KWOTĘ TRANSAKCJI !"
            validationCost.style.display='block';
            formCostInput.style= "border-color:var(--validation-color);";
          }

        }else if(name.charAt(0,1) === ' '){
          if(costTrans.length !== 0 && costTrans !== '.' ){
            validationCost.style.display='none';
            formCostInput.style= "border-color:var(--border-color)";
          }else{
            validationCost.style.display='block';
            formCostInput.style= "border-color:var(--validation-color);";
          }
          validationName.innerHTML = " NAZWA NIE MOŻE ZACZYNAĆ SIĘ OD SPACJI";
          validationName.style.display='block';
          formNameInput.style= "border-color:var(--validation-color);";

          if(costTrans.length === 0){
            if(name.length !== 0  && name.charAt(0,1) !== ' '){
              validationName.style.display='none';
              formNameInput.style= "border-color:var(--validation-color)";
            }
            validationCost.innerHTML = "PODAJ KWOTĘ TRANSAKCJI !"
            validationCost.style.display='block';
            formCostInput.style= "border-color:var(--validation-color);";
          }else if(costTrans === '.'){
            if(name.length !== 0  && name.charAt(0,1) !== ' '){
              validationName.style.display='none';
              formNameInput.style= "border-color:var(--border-color)";
            }
            validationCost.innerHTML = "PODAJ PRAWIDŁOWĄ KWOTĘ TRANSAKCJI !"
            validationCost.style.display='block';
            formCostInput.style= "border-color:var(--validation-color);";
          }

        }else if(costTrans.length === 0){
          if(name.length !== 0  && name.charAt(0,1) !== ' '){
            validationName.style.display='none';
            formNameInput.style= "border-color:var(--validation-color)";
          }
          validationCost.innerHTML = "PODAJ KWOTĘ TRANSAKCJI !"
          validationCost.style.display='block';
          formCostInput.style= "border-color:var(--validation-color);";
        }else if(costTrans === '.'){
          if(name.length !== 0  && name.charAt(0,1) !== ' '){
            validationName.style.display='none';
            formNameInput.style= "border-color:var(--border-color)";
          }
          validationCost.innerHTML = "PODAJ PRAWIDŁOWĄ KWOTĘ TRANSAKCJI !"
          validationCost.style.display='block';
          formCostInput.style= "border-color:var(--validation-color);";
        }else{
          const obj ={
            name,
            costTrans
          }
          this.state.listTrans.push(obj)
          this.setState({
            listTrans: this.state.listTrans,
            actualValueInputCost: ''
          })
         const done_trans = document.querySelector("#Done__Transaction")
          done_trans.style.display ="block";
          formNameInput.style= "border-color:var(--done-color)";
          formCostInput.style= "border-color:var(--done-color)";
          formNameInput.value = "";
          validationCost.style.display='none';
          validationName.style.display='none';
          setTimeout(this.finishDoneForm,1500,done_trans,formNameInput,formCostInput)
        }  
  }

  finishDoneForm = (done_trans,formNameInput,formCostInput) =>{
    done_trans.style.display ="none";
    formNameInput.style= "border-color:var(--border-color)";
    formCostInput.style= "border-color:var(--border-color)";
  }

  delOneTrans = (index) =>{
    this.state.listTrans.splice(index, 1)
    this.setState({
      listTrans: this.state.listTrans
    }) 
  }

  delAllTrans = () =>{
    this.setState({
      listTrans: []
    })
  }
 

  changeValueInput = (numb,e) =>{
    let actualValueInput = e.currentTarget.value;
    let reg = 0;
    if(numb=== 1){
       reg = /^(([0-9]{0,2})(\.[0-9]{0,4})?$)/;
    }else if(numb=== 2){
       reg = /^(([0-9]{0,6})(\.[0-9]{0,4})?$)/;
    }
    if (actualValueInput.match(reg) != null) {
      actualValueInput = actualValueInput.match(reg)[0]
      if(numb === 1){
        this.setState({
          actualValueInputValute: actualValueInput 
        })
      }else if(numb === 2){
        this.setState({
          actualValueInputCost: actualValueInput 
        })
      }
    }
  }

  changeValute = () =>{
    const newValuteValue = document.querySelector('#ValuteValue').value;
    const validation = document.querySelector('.Valute__Validation');
    const valuteValueInput  = document.querySelector('#ValuteValue');
    if(newValuteValue > 0){
      if(parseFloat(newValuteValue) !== parseFloat(this.state.valute)){
        this.setState({
          valute: parseFloat(newValuteValue),
          actualValueInputValute: ''
        })
        validation.innerHTML = 'POMYŚLNIE ZMIENIONO WALUTĘ !'
        validation.style.display='block';
        validation.style.color='var(--done-color)';
        valuteValueInput.style= "border-color:var(--done-color)";
        setTimeout(this.addDone,1500,valuteValueInput,validation)
      }else{
        validation.innerHTML = 'PODAJ INNĄ WARTOŚĆ OD AKTUALNEJ !'
        validation.style.display='block';
        valuteValueInput.style= "border-color:var(--validation-color)";
      }
    }else{
      if(newValuteValue === '.'){
        validation.innerHTML = 'WPISZ PRAWIDŁOWĄ WARTOŚĆ WALUTY !'
        validation.style.display='block';
        valuteValueInput.style= "border-color:var(--validation-color)";
      }else{
        validation.innerHTML = 'PODAJ WARTOŚĆ WALUTY !'
        validation.style.display='block';
        valuteValueInput.style= "border-color:var(--validation-color)";
      }

    }

  }

  addDone = (e,validation) =>{
        validation.style.display='none';
        validation.style.color='var(--validation-color)';
        e.style= "border-color:var(--border-color)";
        e.value = "";
  }

  backValute = () =>{
    this.setState({
      valute: this.state.defValute
    })
  }
  render(){
    return (
      <div className="App_body">
      <Valute changeValute={this.changeValute} actualValueInput={this.state.actualValueInputValute} backValute={this.backValute} changeValueInput={this.changeValueInput.bind(this)} actualyValute={this.state.valute} defValute={this.state.defValute}/>
      <TransactionForm actualValueInput={this.state.actualValueInputCost} changeValueInput={this.changeValueInput.bind(this)} addTrans={this.addTrans.bind(this)}/>
      <TransactionList delAllTrans={this.delAllTrans} delOneTrans={this.delOneTrans} actualyValute={this.state.valute} listTrans={this.state.listTrans} />
      </div>
    );
  }
}

export default App;
